.breadcrumb {
    li {
        color: $grey-03;
        display: inline-block;
        a {
            color: $grey-02;
            &:hover {
                color: $grey-01; } } }
    li+li {
        &::before {
            color: $grey-03;
            padding: 4px;
            content: "\25B8"; } }
    @media screen and ( max-width: 800px ) {
        display: none; } }

