* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: none;
  list-style-type: none;
  outline: none; }

html, body {
  height: 100%;
  font-size: 100%;
  font-family: Public Sans ,sans-serif;
  line-height: 1.4; }

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  font-size: 0;
  height: 0;
  visibility: hidden; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none;
  @include transition(150ms); }
