body {
    width: 100%;
    color: $black-01;
    background: $white-02; }
.container {
    max-width: 1440px;
    width: 90%;
    margin: 0 auto; }
.icon {
    vertical-align: bottom; }
#hero {
    padding-top: 100px;
    background-color: $blue-04;
    background: $blue-04 url("../../images/bg-dark.png") no-repeat center {
        size: cover; } }

// Password Label Instruction
.set-password, .nested-label {
    .label-01 {
        margin-left: 8px;
        color: $grey-03; } }
// Bouncer Form Validation Message Styling
.error {
    border-color: $danger-red !important; }
.error-message {
    color: $danger-red;
    margin: 2px auto 1em; }
